<template>
    <section>
        <!--工具条-->
        <el-col :span="8" class="toolbar roles">
            <el-card class="box-card left-aside">
                <div slot="header" class="clearfix">
                    <span>权限</span>
                    <el-button
                        @click="getRoles"
                        style="float: right; padding: 3px 0"
                        type="text"
                        >刷新</el-button
                    >
                </div>
                <div
                    v-for="o in roles"
                    :key="o.Id"
                    @click="operate(o.Id)"
                    :class="o.Id == roleid ? 'active' : ''"
                    class="left-item"
                >
                    <div>
                        <i class="el-icon-price-tag"></i>
                        {{ o.Name }}
                    </div>
                </div>
            </el-card>
        </el-col>
        <el-col :span="16" class="toolbar perms morechildren">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span>菜单</span>
                    <el-button
                        :loading="loadingSave"
                        @click="saveAssign"
                        style="float: right; padding: 3px 0"
                        type="text"
                        >{{ loadingSaveStr }}</el-button
                    >
                </div>
                <div class="block">
                    <el-tree
                        :data="data5"
                        show-checkbox
                        node-key="value"
                        ref="tree"
                        default-expand-all
                        :expand-on-click-node="true"
                        :check-strictly="true"
                        @check-change="checkChange"
                    >
                        <span
                            class="custom-tree-node"
                            slot-scope="{ node, data }"
                        >
                            <span class="node-label">{{ node.label }}</span>
                            <span class="node-btn">
                                <el-checkbox-group v-model="assignBtns">
                                    <el-checkbox
                                        v-for="btn in data.btns"
                                        :key="btn.value"
                                        :label="btn.value.toString()"
                                        :disabled="btn.disabled ? true : false"
                                        @change="
                                            (checked) =>
                                                subButtonChange(checked, data)
                                        "
                                    >
                                        {{ btn.label }}
                                    </el-checkbox>
                                </el-checkbox-group>
                            </span>
                        </span>
                    </el-tree>
                </div>
            </el-card>
        </el-col>
    </section>
</template>

<script>
import util from "../../../util/date";
import {
    getRoleAll,
    getPermissionTree,
    getPermissionIds,
    addRolePermission,
} from "../../api/api";

let id = 1000;

export default {
    data() {
        const data = [];
        return {
            roles: [],
            roleid: 0,
            data5: [],
            btns: [],
            assigns: [],
            checked1: false,
            loadingSaveStr: "保存",
            loadingSave: false,
            assignBtns: [],
            defaultProps: {
                children: "children",
                label: "label",
                btns: "btns",
            },
            selectedPermissions: [],
            currentRoleCode: "",
            stores: {
                role: {
                    data: [],
                },
                permissionTree: {
                    data: [],
                },
            },
            buttonProps: {
                type: "default",
                size: "small",
            },

            selectRole: {},
            menuData: [],
            menuSelections: [],
            menuLoading: false,
            authLoading: false,
            checkAll: false,
            currentRoleMenus: [],
        };
    },
    computed: {
        filterRoles: function () {
            return this.roles.filter((r) => {
                return !r.issys;
            });
        },
    },
    methods: {
        //获取角色列表
        getRoles() {
            getRoleAll().then((res) => {
                this.roles = res.data;
                this.getPermissions();
            });
        },
        //获取菜单树
        getPermissions() {
            let _this = this;
            let para = { needbtn: false };
            getPermissionTree(para).then((res) => {
                _this.loadingSave = false;
                _this.loadingSaveStr = "保存";
                this.data = res.data.children;
                this.data5 = JSON.parse(JSON.stringify(this.data));
            });
        },
        //获取菜单Id，通过角色id
        getPermissionIds(rid) {
            let _this = this;
            this.assigns = [];
            this.assignBtns = [];
            let para = { rid: rid };
            getPermissionIds(para).then((res) => {
                _this.loadingSave = false;
                _this.loadingSaveStr = "保存";
                this.$refs.tree.setCheckedKeys(res.data.permissionids);
                this.assignBtns = res.data.assignbtns;
            });
        },
        operate(id) {
            this.loadingSave = true;
            this.loadingSaveStr = "加载中...";
            this.roleid = id;
            this.getPermissionIds(id);
        },
        saveAssign() {
            let _this = this;
            this.loadingSave = true;
            this.loadingSaveStr = "保存中...";
            let pids = this.$refs.tree.getCheckedKeys();
            try {
                if (this.assignBtns.length > 0) {
                    for (let i = 0; i < this.assignBtns.length; i++) {
                        // let assginbtn = this.assignBtns[i].split("_")[1];
                        let assginbtn = this.assignBtns[i];
                        if (assginbtn) {
                            pids.push(assginbtn);
                        }
                    }
                }
            } catch (e) {}
            let para = { pids: pids, rid: this.roleid };
            if (para && para.pids.length > 0) {
                addRolePermission(para).then((res) => {
                    _this.loadingSave = false;
                    _this.loadingSaveStr = "保存";

                    if (res.success) {
                        this.$message({
                            message: "保存成功",
                            type: "success",
                        });

                        let para = { rid: this.roleid };
                        getPermissionIds(para).then((res) => {
                            this.$refs.tree.setCheckedKeys(
                                res.data.permissionids
                            );
                            this.assignBtns = res.data.assignbtns;
                            this.$message({
                                message: "数据更新成功",
                                type: "success",
                            });
                        });
                    } else {
                        this.$message({
                            message: res.msg,
                            type: "error",
                        });
                    }
                });
            } else {
                this.loadingSaveStr = "保存";
                this.loadingSave = false;
                this.$message({
                    message: "参数错误",
                    type: "error",
                });
            }
        },
        append(data) {
            const newChild = { id: id++, label: "testtest", children: [] };
            if (!data.children) {
                this.$set(data, "children", []);
            }
            data.children.push(newChild);
        },

        remove(node, data) {
            const parent = node.parent;
            const children = parent.data.children || parent.data;
            const index = children.findIndex((d) => d.id === data.id);
            children.splice(index, 1);
        },
        subButtonChange(val, data) {
            if (val) {
                this.$refs.tree.setChecked(data, true);
            }
        },
        checkChange(data, check) {
            //先取消按钮
            if (!check) {
                if (data.btns) {
                    for (var b = 0; b < data.btns.length; b++) {
                        var index = this.assignBtns.indexOf(data.btns[b].value);
                        if (index > -1) {
                            this.assignBtns.splice(index, 1);
                        }
                    }
                }
            }
            // 父节点操作
            if (data.Pid !== null) {
                if (check === true) {
                    // 如果选中，设置父节点为选中
                    this.$refs.tree.setChecked(data.Pid, true);
                } else {
                    // 如果取消选中，检查父节点是否该取消选中（可能仍有子节点为选中状态）
                    var parentNode = this.$refs.tree.getNode(data.Pid);
                    var parentHasCheckedChild = false;
                    if (parentNode) {
                        for (
                            var i = 0,
                                parentChildLen = parentNode.childNodes.length;
                            i < parentChildLen;
                            i++
                        ) {
                            if (parentNode.childNodes[i].checked === true) {
                                parentHasCheckedChild = true;
                                break;
                            }
                        }
                    }
                    if (!parentHasCheckedChild) {
                        this.$refs.tree.setChecked(data.Pid, false);
                    }
                }
            }
            // 子节点操作，如果取消选中，取消子节点选中
            if (data.children != null && check === false) {
                for (var j = 0, len = data.children.length; j < len; j++) {
                    var childNode = this.$refs.tree.getNode(
                        data.children[j].value
                    );
                    if (childNode && childNode.checked === true) {
                        this.$refs.tree.setChecked(childNode.data.value, false);
                    }
                }
            }
        },
    },
    mounted() {
        this.loadingSave = true;
        this.loadingSaveStr = "加载中...";
        this.getRoles();

        // this.getPermissions();
    },
};
</script>

<style>
.role-item {
    cursor: pointer;
    padding: 10px;
}

.role-item.active {
    background: #ebf5ff;
}

.role-item:hover {
    background: #ebf5ff;
}

.custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    font-size: 14px;
    padding-right: 8px;
}
.custom-tree-node .node-label {
    flex-grow: 1;
    flex-shrink: 1;
    min-width: 200px;
    display: inline-block;
}
.custom-tree-node .node-btn {
    flex-grow: 4;
    flex-shrink: 4;
    display: inline-block;
}
.custom-tree-node .node-btn .el-checkbox {
    color: #c2c2c2;
}
.text {
    font-size: 14px;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both;
}

.box-card {
    width: 90%;
}

.morechildren .el-checkbox {
    margin-right: 5px !important;
    float: left;
}
.morechildren .el-checkbox-group {
    /* margin-left: 50px; */
    padding: 5px;
}
.morechildren .el-tree-node__content {
    height: auto !important;
}
.morechildren .el-tree-node__content:hover {
    background: #ecf5ff;
}
</style>



